import { useState } from "react";

export default function ChangePassword() {
    const [form, setForm] = useState({
        currentPassword: "",
        newPassword: "",
        confirmPassword: "",
    })

    const [error, setError] = useState("")
    const [success, setSuccess] = useState("")

    const  handleChange = (e) => {
        setForm({
            ...form,
            [e.target.name]: e.target.value
        })
    }

    const handleSubmit = (e) => {
        e.preventDefault();
        setError("");
        setSuccess("");

        const { currentPassword, newPassword, confirmPassword } = form;

        if (!currentPassword || !newPassword || !confirmPassword) {
            return setError("Vui lng nhp y  thng tin");
        }

        if (newPassword.length < 8) {
            return setError("Mt khu phi c t nht 8 k t");
        }

        if (newPassword === currentPassword) {
            return setError("Mt khu mi khng c trng mt khu c");
        }

        if (newPassword !== confirmPassword) {
            return setError("Xc nhn mt khu khng khp");
        }

        setSuccess("i mt khu thnh cng");
        setForm({
            currentPassword: "",
            newPassword: "",
            confirmPassword: "",
        });
    };

    return (
        <div className="flex flex-col items-center py-5">
            <h1 className="text-2xl font-semibold mb-6">
                i mt khu
            </h1>

            <form 
                onSubmit={handleSubmit}
                className="flex flex-col bg-white shadow rounded-xl p-6"
            >
                <Input 
                    label="Mt khu hin ti"
                    name="currentPassword"
                    type="password"
                    value={form.currentPassword}
                    onChange={handleChange} 
                />

                <Input 
                    label="Mt khu mi"
                    name="newPassword"
                    type="password"
                    value={form.newPassword}
                    onChange={handleChange} 
                />

                <Input 
                    label="Xc nhn mt khu mi"
                    name="confirmPassword"
                    type="password"
                    value={form.confirmPassword}
                    onChange={handleChange} 
                />

                {error && (
                    <p className="text-red-500 text-sm">{error}</p>
                )}

                {success && (
                    <p className="text-green-600 text-sm">{success}</p>
                )}

                <button
                    type="submit"
                    className="w-full mt-6 bg-blue-500 text-white py-2 rounded-lg hover:bg-blue-700 transition"
                >
                    Cp nht mt khu
                </button>
            </form>
        </div>
    )
}

function Input({ label, ...props }) {
    return (
        <div>
            <label className="block mb-1 text-sm font-medium">
                {label}
            </label>

            <input 
                {...props}
                className="w-full border rounded-full border border-blue-500 px-3 py-2 focus:outline-none focus:ring-2 focus:ring-blue-500"
            />
        </div>
    )
}